body {
  margin: 0;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  color: var(--color-text);
  background-color: var(--bg-body);
  min-width: 320px;
}

img {
  max-width: 100%;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
  overflow: hidden;
  border: none;
}

h1 {
  font-weight: 600;
}

.container {
  max-width: var(--page-max-width);
  margin: 0 auto;
  padding: 0 var(--offset-page);
}

:root {
  --transition-duration: 250ms;
  --transition-function: ease-in-out;
  --color-text: #212121;
  --bg-body: #F5F4EF;
  --page-max-width: 1500px;
  --offset-page-top: 12px;
  --offset-page: 12px;
  --offset-slider: 2px;
}

@font-face {
  font-weight: 400;
  font-family: 'Montserrat';
  font-style: normal;
  src: url(../fonts/Montserrat-Regular.woff2) format('woff2'), url(../fonts/Montserrat-Regular.woff) format('woff');
  font-display: swap;
}

@font-face {
  font-weight: 600;
  font-family: 'Montserrat';
  font-style: normal;
  src: url(../fonts/Montserrat-SemiBold.woff2) format('woff2'), url(../fonts/Montserrat-SemiBold.woff) format('woff');
  font-display: swap;
}

@font-face {
  font-weight: 600;
  font-family: 'Random Grotesque Standard';
  font-style: normal;
  src: url(../fonts/RG-StandardSemibold.woff2) format('woff2'), url(../fonts/RG-StandardSemibold.woff) format('woff');
  font-display: swap;
}

.main__header {
  padding-top: 32px;
  padding-bottom: 24px;
}

.main__header-title {
  position: relative;
  width: fit-content;
  margin: 0 0 12px;
  padding-right: 24px;
  font-weight: 600;
  font-size: 78px;
  line-height: 108%;
  line-height: 84px;
  font-family: 'Random Grotesque Standard', Arial, Helvetica, sans-serif;
  text-transform: uppercase;
  font-style: normal;
  letter-spacing: -0.01em;
}

.main__header-title sup {
  position: absolute;
  top: 0;
  right: 0;
  font-weight: 400;
  font-size: 32px;
  line-height: 39px;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
}

.main__header-subtitle {
  margin: 0 0 32px;
  font-size: 12px;
  line-height: 15px;
}

.main__header-list-tags {
  max-width: 300px;
}

.list-tags {
  --offset-link: 4px;
  display: inline-flex;
  flex-wrap: wrap;
  margin: 0;
  margin-right: calc(var(--offset-link)*-1);
}

.list-tags span,
.list-tags a {
  display: block;
  margin-right: var(--offset-link);
  margin-bottom: var(--offset-link);
  padding: 4px 10px 5px;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  color: inherit;
  text-transform: lowercase;
  text-decoration: none;
  background-color: var(--bg-body);
  border: 1px solid #212121;
  border-radius: 12px;
}

.button-arrow {
  box-sizing: border-box;
  position: relative;
  display: inline-block;
  width: 65px;
  height: 21px;
  padding: 0;
  overflow: hidden;
  background-color: var(--bg-body);
  border: 1px solid var(--color-text);
  border-radius: 12px;
  appearance: none;
}

.button-arrow::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-text);
  mask-position: center;
  mask-repeat: no-repeat;
  mask-size: 21px auto;
}

.button-arrow--left::before {
  mask-image: url(../img/icons/arrow-left.svg);
}

.button-arrow--right::before {
  mask-image: url(../img/icons/arrow-right.svg);
}

.button {
  display: inline-block;
  box-sizing: border-box;
  padding: 8px 40px 9px;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: inherit;
  text-decoration: none;
  background-color: var(--bg-body);
  border: 1px solid #212121;
  border-radius: 24px;
  appearance: none;
}

.soc-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, 32px);
  margin: 0;
  padding: 0;
  list-style-type: none;
  gap: 12px;
}

.soc-list__link {
  position: relative;
  display: block;
  overflow: hidden;
  border-radius: 50%;
}

.soc-list__link::before {
  content: '';
  display: block;
  width: 32px;
  height: 32px;
  background-color: var(--bg-body);
  mask-repeat: no-repeat;
  mask-position: center;
  mask-size: 32px;
}

.soc-list__link--vk::before {
  mask-image: url(../img/social/vk.svg);
}

.soc-list__link--yt::before {
  mask-image: url(../img/social/yt.svg);
}

.soc-list__link--tg::before {
  mask-image: url(../img/social/tg.svg);
}

.link-animate {
  overflow: hidden;
}

.link-animate span {
  display: block;
  transition: opacity var(--transition-duration) var(--transition-function), transform var(--transition-duration) var(--transition-function);
}

.link-animate .link-animate-hover {
  opacity: 0;
}

.link-animate .link-animate-view {
  opacity: 1;
}

.link-animate:hover span {
  transform: translateY(-100%);
}

.link-animate:hover .link-animate-hover {
  opacity: 1;
}

.link-animate:hover .link-animate-view {
  opacity: 0;
}

.header {
  border-bottom: 1px solid #DDDCD6;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: var(--offset-page-top);
  padding-bottom: var(--offset-page-top);
}

.header__logo img {
  width: 82px;
  height: 47px;
}

.header__link,
.nav__link {
  --line-height: 17px;
  display: block;
  height: var(--line-height);
  padding: 4px 10px 5px;
  font-weight: 600;
  font-size: 14px;
  line-height: var(--line-height);
  color: inherit;
  text-transform: lowercase;
  text-decoration: none;
}

.header__link span,
.nav__link span {
  height: var(--line-height);
}

.nav__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.work {
  overflow: hidden;
}

.work__container {
  max-width: calc(var(--page-max-width) + var(--offset-page) * 2);
  margin: 0 auto;
  padding-bottom: 64px;
}

.work__slider__list {
  margin: 0 0 12px;
  padding: 0;
}

.slick-list {
  overflow: visible;
}

.work__slider__item {
  padding: 0 var(--offset-slider);
}

.work__slider__item img {
  display: block;
  width: 100%;
  border-radius: 10px;
}

.work__slider__button-wrap {
  text-align: center;
}

.work__slider__button-prev {
  margin-right: 12px;
}

.no-js .work__slider__button-prev,
.no-js .work__slider__button-next {
  display: none;
}

.no-js .work__slider__list {
  display: flex;
  align-items: flex-start;
  box-sizing: border-box;
  width: 100%;
  max-width: calc(var(--page-max-width) - var(--offset-page) * 2);
  margin-right: auto;
  margin-left: auto;
  padding-bottom: 20px;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}

.no-js .work__slider__item {
  display: flex;
  flex-shrink: 0;
  flex-basis: 100%;
  scroll-snap-align: center;
}

.portfolio {
  padding-bottom: 74px;
}

.portfolio__title {
  position: relative;
  margin: 0 0 32px;
  font-weight: 600;
  font-size: 32px;
  line-height: 1;
  font-family: 'Random Grotesque Standard', Arial, Helvetica, sans-serif;
  text-transform: uppercase;
}

.portfolio__title-animate {
  position: relative;
  display: block;
  height: 32px;
}

.portfolio__title-animate span {
  --duration: 15s;
  position: absolute;
  top: 0;
  left: 0;
  color: #f15b40;
  perspective: 100px;
  opacity: 0;
  animation-name: portfolio-title;
  animation-duration: var(--duration);
  animation-timing-function: var(--transition-function);
  animation-iteration-count: infinite;
}

.portfolio__title-animate span:nth-child(2) {
  animation-delay: calc(var(--duration)/3);
}

.portfolio__title-animate span:nth-child(3) {
  animation-delay: calc(var(--duration)/3 * 2);
}

@keyframes portfolio-title {
  0% {
    opacity: 0;
  }

  3% {
    opacity: 1;
  }

  32% {
    opacity: 1;
  }

  35% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

.portfolio__subtitle {
  margin: 0 0 32px;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
}

.portfolio__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.portfolio__item:hover img {
  transform: scale(1.1);
}

.portfolio__item {
  margin-bottom: 24px;
}

.portfolio__item a {
  color: inherit;
  text-decoration: none;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}

.portfolio__item img {
  display: block;
  width: 100%;
  height: 100%;
  transition: transform var(--transition-duration) var(--transition-function);
  object-fit: cover;
}

.portfolio__picture {
  display: block;
  width: 100%;
  margin-bottom: 12px;
  overflow: hidden;
  border-radius: 12px;
  aspect-ratio: 74/55;
}

.portfolio__list-tags {
  position: absolute;
  top: 12px;
  left: 12px;
  z-index: 1;
}

.portfolio__list-title {
  width: calc(100% - 12px - 65px);
  margin: 0;
  margin-right: 12px;
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
  font-family: 'Random Grotesque Standard', Arial, Helvetica, sans-serif;
}

.portfolio__button {
  display: block;
  max-width: 296px;
  margin: 0 auto;
  text-transform: lowercase;
}

.services {
  padding-bottom: 74px;
}

.services__content {
  font-weight: 600;
  padding: 0 3px;
  font-size: 32px;
  line-height: 1;
  font-family: 'Random Grotesque Standard', Arial, Helvetica, sans-serif;
  text-transform: uppercase;
}

.services__left {
  display: block;
}

.services__right {
  width: fit-content;
}

.services__right > span {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.services__right > span:not(:last-child) {
  margin-bottom: 32px;
}

.services__button-arrow {
  margin-bottom: -5px;
  margin-left: 12px;
  opacity: 0;
  transition: opacity var(--transition-duration) var(--transition-function);
}

.services__right > span:hover .services__button-arrow {
  opacity: 1;
}

.feedback {
  padding-bottom: 64px;
}

.feedback__wrap {
  position: relative;
}

.feedback__wrap:not(:last-child) {
  margin-bottom: 12px;
}

.feedback__content {
  padding: 12px 12px 14px;
  color: var(--bg-body);
  background-color: var(--color-text);
  border-radius: 12px;
}

.feedback__title {
  margin: 0;
  font-weight: 600;
  font-size: 32px;
  line-height: 1;
  font-family: "Random Grotesque Standard", Arial, Helvetica, sans-serif;
}

.feedback__title--contact {
  margin-bottom: 24px;
}

.feedback__subtitle {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
}

.feedback__form-left {
  margin-right: 12px;
}

.feedback__input {
  display: block;
  width: 100%;
  max-width: 344px;
  margin-bottom: 16px;
  padding: 8px 0;
  font-size: 12px;
  line-height: 15px;
  color: inherit;
  background-color: transparent;
  border: 0;
  border-bottom: 1px solid #454545;
  appearance: none;
}

.feedback__label-checkbox {
  position: relative;
  display: block;
  box-sizing: border-box;
  min-height: 24px;
  padding-left: 35px;
  font-size: 12px;
  line-height: 15px;
}

.feedback__label-checkbox::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  width: 24px;
  height: 24px;
  border: 1px solid var(--bg-body);
  border-radius: 6px;
}

.feedback__submit {
  box-sizing: border-box;
  margin-top: 23px;
  padding: 8px 14px 9px;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  font-family: inherit;
  color: inherit;
  text-transform: lowercase;
  background-color: transparent;
  border: 1px solid #F5F4EF;
  border-radius: 24px;
  appearance: none;
}

.feedback__address {
  display: flex;
  margin-bottom: 64px;
  font-style: normal;
}

.feedback__address .feedback__link {
  margin-right: 12px;
}

.feedback__link {
  font-size: 14px;
  line-height: 17px;
  color: inherit;
  text-decoration: none;
}

.feedback__wrap:nth-of-type(2)::before {
  content: '';
  display: block;
  width: 100%;
  height: 126px;
  margin-bottom: 12px;
  background-image: url(../img/feedback/feedback-2@xs.jpg);
  background-image: image-set(url(../img/feedback/feedback-2@xs.avif) type("image/avif"), url(../img/feedback/feedback-2@xs.webp) type("image/webp"));
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 12px;
}

.footer {
  border-top: 1px solid #DDDCD6;
}

.footer__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding-top: 24px;
  padding-bottom: 17px;
  gap: 24px;
}

.footer__link {
  font-size: 12px;
  line-height: 15px;
  color: inherit;
  text-decoration: none;
}

.footer__link--dis {
  text-align: center;
  display: block;
  width: fit-content;
  margin-left: auto;
}

@media (min-width: 576px) {
  .portfolio__subtitle {
    font-size: 14px;
    line-height: 17px;
  }

  .feedback__wrap:nth-of-type(2) {
    display: flex;
    justify-content: space-between;
  }

  .feedback__wrap:nth-of-type(2) .feedback__content {
    min-width: 300px;
  }

  .feedback__wrap:nth-of-type(2)::before {
    height: auto;
    margin-right: 12px;
    margin-bottom: 0;
    background-image: url(../img/feedback/feedback-2@md.jpg);
    background-image: image-set(url(../img/feedback/feedback-2@md.avif) type("image/avif"), url(../img/feedback/feedback-2@md.webp) type("image/webp"));
  }
}

@media (min-width: 600px) {
  .portfolio__picture {
    aspect-ratio: 576/245;
  }

  .portfolio__grid {
    display: flex;
  }

  .portfolio__grid .portfolio__item:first-child {
    width: 126.87%;
    margin-right: 24px;
  }

  .portfolio__grid .portfolio__item:last-child {
    width: 180.24%;
  }

  .portfolio__grid:nth-of-type(4n + 1) .portfolio__item:last-child {
    width: 126.87%;
  }

  .portfolio__grid:nth-of-type(4n + 1) .portfolio__item:first-child {
    width: 180.24%;
  }

  .portfolio__grid .portfolio__item:last-child .portfolio__picture,
  .portfolio__grid:nth-of-type(4n+1) .portfolio__item:first-child .portfolio__picture {
    aspect-ratio: 331/245;
  }

  .portfolio__grid .portfolio__item:first-child .portfolio__picture,
  .portfolio__grid:nth-of-type(4n+1) .portfolio__item:last-child .portfolio__picture {
    aspect-ratio: 233/245;
  }
}

@media (min-width: 768px) {
  :root {
    --offset-page: 32px;
    --offset-page-top: 13px;
  }

  :root {
    --offset-slider: 6px;
  }

  .main__header {
    display: flex;
  }

  .main__header-title {
    margin-bottom: 0;
    margin-right: 15px;
    font-size: 125px;
  }

  .main__header-subtitle {
    margin-bottom: 12px;
    font-size: 14px;
    line-height: 17px;
  }

  .list-tags {
    --offset-link: 8px;
  }

  .button-arrow {
    width: 74px;
    height: 24px;
  }

  .button-arrow::before {
    mask-size: 24px auto;
  }

  .header__logo img {
    width: 116px;
    height: 66px;
  }

  .header__nav-toggle {
    display: none;
  }

  .header__right,
  .header__logo {
    flex-shrink: 0;
    width: 200px;
  }

  .header__right {
    margin-right: -10px;
    margin-left: 10px;
    text-align: right;
  }

  .nav__list {
    justify-content: center;
    width: fit-content;
    margin: 0 auto;
  }

  .work__slider__list {
    margin-bottom: 22px;
  }

  .work__slider__button-wrap {
    padding-right: calc(178px - var(--offset-page));
    text-align: right;
  }

  .work__slider__button-prev {
    margin-right: 8px;
  }

  .portfolio__title {
    font-size: 48px;
  }

  .portfolio__title-animate {
    height: 48px;
  }

  .portfolio__item {
    margin-bottom: 56px;
  }

  .portfolio__list-title {
    width: calc(100% - 12px - 74px);
  }

  .services__content {
    position: relative;
    display: flex;
    align-items: flex-start;
  }

  .services__left {
    position: sticky;
    top: 50vh;
    display: flex;
    align-items: center;
    white-space: nowrap;
  }

  .services__left::after {
    content: '';
    display: inline-block;
    vertical-align: middle;
    height: 1px;
    width: 51px;
    margin: 0 27px;
    background-color: var(--color-text);
  }

  .feedback__wrap:not(:last-child) {
    margin-bottom: 24px;
  }

  .feedback__form {
    width: 53.82%;
    margin-right: 24px;
  }

  .feedback__wrap {
    display: flex;
    justify-content: space-between;
  }

  .feedback__wrap:first-of-type::after {
    content: '';
    display: block;
    width: calc(43.71% - 24px);
    background-image: url(../img/feedback/feedback-1@md.jpg);
    background-image: image-set(url(../img/feedback/feedback-1@md.avif) type("image/avif"), url(../img/feedback/feedback-1@md.webp) type("image/webp"));
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 12px;
  }

  .footer__container {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 850px) {
  .feedback__wrap:nth-of-type(2) .feedback__content {
    width: 45.232%;
  }

  .feedback__wrap:nth-of-type(2)::after {
    content: '';
    display: block;
    width: 15.08%;
    min-width: 172px;
    margin-left: 24px;
    background-image: url(../img/feedback/feedback-3@md.jpg);
    background-image: image-set(url(../img/feedback/feedback-3@md.avif) type("image/avif"), url(../img/feedback/feedback-3@md.webp) type("image/webp"));
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 12px;
  }

  .feedback__wrap:nth-of-type(2)::before {
    margin-right: 24px;
  }
}

@media (min-width: 990px) {
  :root {
    --offset-page: 64px;
  }

  .main__header {
    padding-top: 54px;
    padding-bottom: 40px;
  }

  .main__header-right {
    margin-left: auto;
    max-width: 277px;
  }

  .main__header-title {
    margin-right: 30px;
  }

  .list-tags span,
  .list-tags a {
    font-size: 14px;
    line-height: 17px;
  }

  .soc-list {
    gap: 24px;
  }

  .work__container {
    padding-bottom: 84px;
  }

  .portfolio {
    padding-bottom: 132px;
  }

  .portfolio__title {
    margin-bottom: 48px;
  }

  .portfolio__list-title {
    font-size: 24px;
    line-height: 1;
  }

  .services {
    padding-bottom: 132px;
    padding-left: calc(var(--offset-page) - 8px);
    padding-right: calc(var(--offset-page) - 8px);
  }

  .feedback {
    padding-bottom: 132px;
  }

  .feedback__content {
    padding: 24px;
  }

  .feedback__form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-end;
  }

  .feedback__title {
    max-width: 350px;
    font-size: 48px;
  }

  .feedback__subtitle {
    max-width: 148px;
    font-size: 14px;
    line-height: 17px;
  }

  .feedback__form-top {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    margin-bottom: 16px;
  }

  .feedback__input {
    font-size: 14px;
    line-height: 17px;
  }

  .feedback__label-checkbox {
    font-size: 14px;
    line-height: 24px;
  }

  .feedback__address .feedback__link {
    margin-right: 54px;
  }

  .feedback__wrap:nth-of-type(2)::before {
    width: 32.26%;
  }
}

@media (min-width: 1024px) {
  .services__content {
    font-size: 48px;
  }
}

@media (min-width: 1280px) {
  .main__header-title {
    font-size: 184px;
    line-height: 89%;
  }

  .main__header-subtitle {
    margin-bottom: 36px;
  }
}

@media (max-width: 989px) {
  .feedback__title {
    margin-bottom: 12px;
  }

  .feedback__subtitle {
    max-width: 206px;
    margin-bottom: 32px;
  }

  .feedback__form-left {
    max-width: 400px;
  }

  .feedback__label-checkbox {
    max-width: 395px;
  }

  .feedback__submit {
    display: block;
    width: 100%;
    max-width: 400px;
  }
}

@media (max-width: 767px) {
  .header__nav-toggle {
    position: relative;
    display: block;
    width: 65px;
    height: 16px;
    padding: 0;
    background-color: transparent;
    border: 1px solid transparent;
    border-top-color: #000;
    border-bottom-color: #000;
    appearance: none;
  }

  .header__nav-toggle::before,
  .header__nav-toggle::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    border-top: 1px solid #000;
    transform: translateY(-50%);
  }

  .header__nav-toggle::after {
    border-top: 0;
  }

  .header__nav-right,
  .nav {
    display: none;
  }

  .services__left {
    margin-bottom: 48px;
  }

  .feedback__form-left {
    max-width: 344px;
  }

  .footer__nav {
    grid-column-end: -1;
    grid-column-start: 1;
    grid-row-start: 1;
  }

  .footer__nav-list {
    flex-direction: column;
    margin-left: 0;
  }
}

@media (max-width: 575px) {
  .services__button-arrow {
    display: none;
  }
}